* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.styles-body {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 40px;
  padding: 52px 40px;
  display: flex;
}

.start-btm {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 16px;
}

.start-btm:hover {
  background-color: #45a049;
  transform: scale(1.05);
}

.start-button:active {
  transform: scale(.95);
}

.stop-btm {
  color: #fff;
  cursor: pointer;
  background-color: #e74c3c;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 16px;
}

.stop-btm:hover {
  background-color: #c64133;
  transform: scale(1.05);
}

.stop-btm:active {
  transform: scale(.95);
}

